@use "variables" as v;

/** Media Queries **/
@mixin tablet {
  @media (min-width: 768px) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin boton($bgColor, $colorTexto) {
  background-color: $bgColor;
  padding: 1.5rem 4rem;
  color: $colorTexto;
  margin-top: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;

  @include tablet {
    width: auto;
  }
  &:hover {
    background-color: darken($bgColor, 10%);
  }
}

@mixin titulo {
  margin-bottom: 2rem;
  text-align: center;

  &::after {
    content: "";
    height: 5rem;
    margin: 0 auto;
    display: block;
    background-image: url("../img/separador.png");
    background-repeat: no-repeat;
    background-position: center;
  }
}
